import "./styles.css";
import Part1 from "./Part1";
import About from "./About";
import Part2 from "./Part2";
import Menu from "./Menu";
import { Component } from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import Web3 from "web3";

class App extends Component {
  componentWillMount() {
    this.loadBlockChainData();
  }

  async loadBlockChainData() {
    const web3 = new Web3(
      Web3.givenProvider ||
        "https://goerli.infura.io/v3/8fc28e07e8e74127a31e06a0af6d86f4"
    );
    const network = await web3.eth.net.getNetworkType();
    const accounts = await web3.eth.getAccounts();
    this.setState({
      account: accounts[0],
      networks: network
    });
    console.log(network);

    console.log(this.state.account);
  }

  state = {
    account: "",
    networks: ""
  };

  render() {
    return (
      <BrowserRouter>
        <Menu />

        <Routes>
          <Route
            exact
            path="/Home"
            key_add={this.state.account}
            element={<Part1 />}
          />
          <Route path="/BuyCoin" element={<Part2 />} />
          <Route path="/OurTechnology" element={<About />} />
        </Routes>
        <h3> your address is {this.state.account}</h3>
        <h3> the network is {this.state.networks}</h3>
      </BrowserRouter>
    );
  }
}

export default App;
